:root{--bg-color:#ddd;--hdr-color:#fff;--logo:calc(240px - 16px);--searchbar:250px;--user-space:calc(300px - 16px);--viewlocation:calc(100% -  var(--logo) - var(--user-space) - var(--searchbar));--sum:calc(var(--searchbar) + var(--viewlocation));--related-action:var(--sum);background-color:var(--bg-color)}#app,body,html{widthv:100%;height:100%;margin:0;padding:0}.disabled{color:rgba(255,255,255,.4)}#hDr .searchbar input[type=text],#hDr .viewlocation ul.views,.navbar ul.sub-menu,.reset{margin:0;padding:0;border:0;outline:0;list-style-type:none}#hDr{width:100%;height:54px;padding:0 16px;-webkit-box-sizing:border-box;box-sizing:border-box;background-color:var(--hdr-color);border-bottom-width:2px;border-bottom-color:var(--hdr-color);border-bottom-style:inset;display:-webkit-box;display:-ms-flexbox;display:flex;box-pack:start;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start}#hDr .logo{width:var(--logo);height:inherit;line-height:54px;font-family:Ubuntu,sans-serif;position:relative}#hDr .viewlocation{width:var(--viewlocation);height:54px;position:relative;overflow:hidden;z-index:1000}#hDr .viewlocation ul.views{width:100%;height:inherit;font:500 14px/44px sans-serif}#hDr .viewlocation ul.views>li.location{display:inline-block;height:inherit;line-height:54px;padding:0 16px;-webkit-box-sizing:border-box;box-sizing:border-box}#hDr .viewlocation ul.views>li.location:first-of-type{border-bottom-width:2px;border-bottom-color:#010056;border-bottom-style:inset}#hDr .viewlocation ul.views>li.location:hover{border-bottom-width:2px;border-bottom-color:#010056;border-bottom-style:inset}#hDr .searchbar{width:var(--searchbar);height:inherit;line-height:54px;padding:0 16px;-webkit-box-sizing:border-box;box-sizing:border-box;position:relative}#hDr .searchbar:first-of-type{border-bottom-width:2px;border-bottom-color:#010056;border-bottom-style:inset}#hDr .searchbar input[type=text]{width:100%;height:36px;color:#585858;background-color:#f5f5f5;margin:9px 0;padding:0 16px;-webkit-box-sizing:border-box;box-sizing:border-box;border-radius:2px;vertical-align:middle;position:relative}#hDr .user-space{width:var(--user-space);height:54px;position:relative}#navbar-show{width:100%;height:290px;background-color:#e02200;-webkit-box-shadow:0 5px 6px rgba(0,0,0,.28),0 3px 2px rgba(0,0,0,.12);box-shadow:0 5px 6px rgba(0,0,0,.28),0 3px 2px rgba(0,0,0,.12)}#navbar-show .slider{width:100%;height:calc(290px - 56px);background-color:rgba(0,0,0,.1)}.navbar{width:100%;height:56px;position:-webkit-sticky;position:sticky}.navbar ul.sub-menu{width:100%;height:inherit;color:#fff;font:500 14px/44px sans-serif}.navbar ul.sub-menu>li.item{display:inline-block;height:inherit;line-height:56px;padding:0 16px;-webkit-box-sizing:border-box;box-sizing:border-box}#container{width:100%;height:1000px;padding:0 16px;-webkit-box-sizing:border-box;box-sizing:border-box}